import { Scene } from './ECS/scene';
import { v2 } from "./maths/v2";
import { Entity } from "./ECS/entity";
import { Camera } from "./components/camera";
import { getCTX, setCanvasesSizes } from './util/rendering';
export function getMousePos(canvas, event) {
    let rect = canvas.getBoundingClientRect();
    const pos = new v2(event.pageX - rect.left - scrollX, rect.height - (event.pageY - rect.top - scrollY));
    const scale = new v2(canvas.width / rect.width, canvas.height / rect.height);
    pos.mul(scale);
    return pos;
}
export function getMousePosWorldSpace(canvas, event) {
    const mousePos = getMousePos(canvas, event);
    return Camera.main
        .getComponent('Camera')
        .screenSpaceToWorldSpace(mousePos, canvas, Camera.main.transform.position);
}
export const input = {
    listen: (type, handler) => {
        document.addEventListener(type, handler);
    },
    'mouseDown': false,
    'cursorPosition': v2.zero,
    'cursorPosWorldSpace': v2.zero
};
document.addEventListener('keydown', event => {
    input[event.key] = true;
});
document.addEventListener('keyup', event => {
    input[event.key] = false;
});
document.addEventListener('keypress', event => {
    Entity.loop(sprite => {
        if (!sprite.hasComponent('GUIElement', 'GUITextBox')) {
            return;
        }
        const element = sprite.getComponent('GUIElement', 'GUITextBox');
        if (element.selected) {
            element.keyPress(event);
        }
    });
});
document.addEventListener('keydown', event => {
    if (event.keyCode !== 8)
        return;
    Entity.loop(sprite => {
        if (!sprite.hasComponent('GUIElement', 'GUITextBox'))
            return;
        const element = sprite.getComponent('GUIElement', 'GUITextBox');
        if (element.selected) {
            element.backspace();
        }
    });
});
export function setMousePos(event, canvas) {
    input.cursorPosition = getMousePos(canvas, event);
    input.cursorPosWorldSpace = getMousePosWorldSpace(canvas, event);
}
export function addEventListeners(canvases, isInitialised) {
    const canvas = canvases.input;
    const div = canvas.parentElement;
    if (!div)
        throw 'no parent to canvas';
    new ResizeObserver(() => {
        setCanvasesSizes(canvases);
    }).observe(div);
    canvas.addEventListener('mousemove', (evt) => {
        if (!isInitialised())
            return;
        setMousePos(evt, canvas);
        Entity.loop(sprite => {
            if (!(sprite.sceneID === Scene.active))
                return;
            for (const component of sprite.components) {
                if (component.type !== 'GUIElement')
                    return;
                const component_ = component;
                component_.hovered = component_.touchingPoint(input.cursorPosition, getCTX(canvas), sprite.transform);
            }
        });
    }, false);
    canvas.addEventListener('mousedown', (evt) => {
        if (!isInitialised())
            return;
        input.mouseDown = true;
        setMousePos(evt, canvas);
        Scene.activeScene.loopThroughScripts((script, sprite) => {
            if (!(sprite.sceneID === Scene.active))
                return;
            if (!sprite.hasComponent('Collider'))
                return;
            let collider = sprite.getComponent('Collider');
            const mousePos = getMousePos(canvas, evt);
            if (!collider.overlapsPoint(sprite.transform, mousePos)) {
                return;
            }
        });
    }, false);
    canvas.addEventListener('keydown', (event) => {
        setMousePos(event, canvas);
    });
    canvas.addEventListener('keyup', (event) => {
        setMousePos(event, canvas);
    });
    canvas.addEventListener('mouseup', (evt) => {
        if (!isInitialised())
            return;
        input.mouseDown = false;
        setMousePos(evt, canvas);
        Scene.activeScene.loopThroughScripts((script, entity) => {
            if (!(entity.sceneID === Scene.active))
                return;
            if (entity.hasComponent('Collider')) {
                let collider = entity.getComponent('Collider');
                const mousePos = getMousePos(canvas, evt);
                if (!collider.overlapsPoint(entity.transform, mousePos)) {
                    return;
                }
                script.runMethod('onMouseUp', entity, []);
            }
            else if (entity.hasComponent('GUIElement')) {
                const ui = entity.getComponent('GUIElement');
                if (ui.hovered) {
                    script.runMethod('onClick', entity, []);
                }
                if (ui.subtype !== 'GUITextBox')
                    return;
                let ui_ = ui;
                ui_.selected = ui_.hovered;
            }
        });
    }, false);
}
